<template>
  <div>

    <el-dialog
      title=""
      :visible.sync="dialogForm.dialogVisible"
      width="50%"
      :before-close="handleClose"
      :close-on-click-modal ="false">
      <div style="height:700px">
        <editor @onEditorChange="onEditorChange" :value="dialogForm.AnalysisContent" style="height:640px"></editor>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogForm.dialogVisible = false" size="small"> </el-button>
        <el-button type="primary" @click="saveAnalysisContent" size="small"> </el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="/"
      :visible.sync="dialogFormUpd.dialogVisible"
      width="30%"
      :before-close="handleClose"
      :close-on-click-modal ="false">
      <div slot="title" style="text-align: left;">
          <div>{{dialogFormUpd.type=='1'?'':''}}</div>
          <hr style="height:1px;border:none;border-top:1px solid #E0E0E0;" />
      </div>      
      <div style="display:table;width:100%;border-collapse:separate; border-spacing:5px 10px;text-align: left;margin-left:20px">
        
        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top"></div>
            <div style="display:table-cell;">
                <span>
                  <el-select size="small" v-model="dialogFormUpd.PositionStatus" placeholder="" style="width:220px">
                    <el-option
                      v-for="item in dialogFormUpd.PositionStatusOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </span>
            </div>
        </div>

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top"></div>
            <div style="display:table-cell">
                <span>
                  <el-date-picker
                    v-model="dialogFormUpd.PositionStartTime"
                    type="datetime"
                    value-format="yyyy-MM-dd HH:ss:mm"
                    size="small">
                  </el-date-picker>
                </span>
            </div>
        </div>        

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top"></div>
            <div style="display:table-cell">
                <span>
                  <el-date-picker
                    v-model="dialogFormUpd.PositionEndTime"
                    type="datetime"
                    value-format="yyyy-MM-dd HH:ss:mm"
                    size="small">
                  </el-date-picker>
                </span>
            </div>
        </div>          

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top"></div>
            <div style="display:table-cell">
                <el-input v-model="dialogFormUpd.CompanyCode" placeholder="" size="small" style="width:220px"></el-input>
            </div>
        </div>      

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top"></div>
            <div style="display:table-cell">
                <el-input v-model="dialogFormUpd.CompanyName" placeholder="" size="small" style="width:220px"></el-input>
            </div>
        </div>      

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top"></div>
            <div style="display:table-cell">
                <el-input v-model="dialogFormUpd.PositionVol" placeholder="" size="small" style="width:220px"></el-input>
            </div>
        </div>    

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top"></div>
            <div style="display:table-cell">
                <el-input v-model="dialogFormUpd.CostPrice" placeholder="" size="small" style="width:220px"></el-input>
            </div>
        </div>    

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top"></div>
            <div style="display:table-cell">
                <el-input v-model="dialogFormUpd.CurrentPrice" placeholder="" size="small" style="width:220px"></el-input>
            </div>
        </div>  

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top"></div>
            <div style="display:table-cell">
                <span>
                  <el-select size="small" v-model="dialogFormUpd.TradeMkPlace" placeholder="" style="width:220px">
                    <el-option
                      v-for="item in dialogFormUpd.TradeMkPlaceOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </span>
            </div>
        </div>                                      

      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormUpd.dialogVisible = false" size="small"> </el-button>
        <el-button type="primary" @click="savePositionAnalysis" size="small"> </el-button>
      </span>
    </el-dialog>    

    <!--  -->
    <div class="boxmain">

      <!--      -->
      <div>

        <div>
          <el-input v-model="formData.CompanyCode" placeholder="" size="mini" style="width:150px"></el-input>
          <el-select v-model="formData.PositionStatus" placeholder="" size="mini" style="width:150px;margin-left:10px" @change="search()">
            <el-option
              v-for="item in formData.PositionStatusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>     
          <el-button type="info" @click="search()" size="mini" style="margin-left:10px"></el-button>   
        </div>

        <div>
          <el-button type="info" @click="viewDialogUpd(1,null)" size="mini" style="margin-top:10px"></el-button>   
        </div>

      </div>
    
      <!---->
      <div style="margin-top:0px;">
        <div style="display:table;width:100%;border-collapse:separate; border-spacing:0px 10px;">
          <div style="display:table-row">
            <div style="display:table-cell">
                <span>{{total}}</span>
            </div>
          </div>
        </div>
      </div>

      <!---->
      <div>

        <!---->
        <el-table v-loading="loading"
            :data="viewData.gridData"
            style="font-size: 13px;width: 100%;margin-bottom: 20px;"
            :header-cell-style="({row, rowIndex})=>{return 'background-color:#F5F5F5;text-align:center;'}"
            :cell-style="({row, column, rowIndex, columnIndex})=>{return 'padding:0px 0px'}"
            @selection-change="handleSelectionChange">
            <el-table-column
              type="index"
              :index="indexMethod"
              width="40">
            </el-table-column>            
            <!-- <el-table-column type="selection"
                          label=""
                          width="30">
            </el-table-column> -->
            <el-table-column label="" prop="" width="50" align="center">
              <template slot-scope="scope">
                <el-link icon="el-icon-edit"  type="primary" @click="viewDialog(scope.row)"></el-link>
                <span v-if="scope.row.AnalysisContent">*</span>
              </template>
            </el-table-column>  
            <el-table-column label="" prop="PositionStatus" width="80" v-if="formData.PositionStatus==''" align="center">
               <template slot-scope="scope">
                <span v-if="scope.row.PositionStatus=='1'"></span>
                <span v-if="scope.row.PositionStatus=='2'"></span>
              </template>
            </el-table-column> 
            <el-table-column label="" prop="PositionStartTime" width="90" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatData(scope.row.PositionStartTime)}}</span>
              </template>
            </el-table-column>    
            <el-table-column label="" prop="PositionEndTime" width="90" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatData(scope.row.PositionEndTime)}}</span>
              </template>              
            </el-table-column>                           
            <el-table-column label="" prop="CompanyCode" width="80" align="center">
            </el-table-column> 
            <el-table-column label="" prop="CompanyName" width="80" align="center">
            </el-table-column>   
            <el-table-column label="" prop="PositionVol" width="80" align="center">
            </el-table-column> 
            <el-table-column label="" prop="CostPrice" width="80" align="center">
            </el-table-column>  
            <el-table-column label="" prop="CurrentPrice" width="80" align="center">
            </el-table-column>  
            <el-table-column label="" prop="" width="80" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatNumFix(((scope.row.CurrentPrice-scope.row.CostPrice)/scope.row.CostPrice)*100,3)}}%</span>
              </template>                
            </el-table-column>  
            <el-table-column label="" prop="" width="80" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatNumFix((scope.row.CurrentPrice*scope.row.PositionVol-scope.row.CostPrice*scope.row.PositionVol),0)}}</span>
              </template>                  
            </el-table-column>  
            <el-table-column label="" prop="" width="80" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatNumFix((scope.row.CurrentPrice*scope.row.PositionVol),0)}}</span>
              </template>                  
            </el-table-column>                                      
            <el-table-column label="" prop="TradeMkPlace" width="100" align="center">
            </el-table-column>                                                                                         
            <el-table-column label="" prop="">
               <template slot-scope="scope">
                <el-button type="info" @click="viewDialogUpd(2,scope.row)" size="mini" style="margin-left:10px"></el-button>   
              </template>
            </el-table-column>                                    
        </el-table>

      </div>

    <!---->
    <div style='margin-top:20px;text-align: center;'>
        <el-pagination background
          layout='total,sizes, prev, pager, next, jumper'
          :total='total'
          :page-sizes="[20,50,100,200]"
          :page-size='pagination.perPageNum'
          :current-page='pagination.curPage'
          @current-change='handleCurrentChange'
          @size-change="handleSizeChange">
        </el-pagination>
    </div>    
    
    </div>

  </div>

</template>
<script>
import editor from '@/components/QuillEditor.vue';
export default {
  name: 'PositionAnalysis',
  data() {
    return {
      fn:'test',      
      posturl:'/PositionAnalysis',
      loading: false,     

      viewData:{
        gridData:[],
        count: 0
      },

      formData:{
        multipleSelection:[],
        CompanyCode:'',
        PositionStatusOptions: [{
                value: '',
                label: '()'
            }, {
                value: '1',
                label: ''                
            }, {
                value: '2',
                label: ''
            }],
        PositionStatus:"1"        
      },

      pagination: {
          perPageNum:20,//
          curPage: 1 //
      },

      dialogForm:{
        dialogVisible: false,
        Id:"",
        AnalysisContent:""
      },

      dialogFormUpd:{
        dialogVisible: false,
        type:'1', //1: 2
        Id:"",
        PositionStatusOptions: [{
                value: 1,
                label: ''                
            }, {
                value: 2,
                label: ''
            }],   
        PositionStatus:1,
        PositionStartTime:'',
        PositionEndTime:'',
        CompanyCode:'',
        CompanyName:'',
        PositionVol:'',
        CostPrice:'',
        CurrentPrice:'',
        TradeMkPlaceOptions: [{
                value: 'A',
                label: 'A'
            }, {
                value: 'A',
                label: 'A'                
            }, {
                value: '',
                label: ''
            }],        
        TradeMkPlace:''
        
      }      
     
    }
  },
  /* vuenew Vue() */
  created () {   

    var _this = this;

    _this.search();

  },
  mounted(){
  },  
  updated(){
  },
  components: { 
    editor
  },
  //
  computed: {
    //
    total:function() {
        var _this = this;
        return Number(_this.viewData.count);
    }
  },
  //
  methods: {

    //
    search() {

      var _this = this;

      try{
        
          _this.loading = true;

        var data = {
            CurPage: _this.pagination.curPage,
            PerPageNum: _this.pagination.perPageNum,
            CompanyCode:_this.formData.CompanyCode,
            PositionStatus:_this.formData.PositionStatus
          }

        _this.$http.post(_this.posturl + "/search",data)
        .then(function (response) {

          if (response.data.Status == "false") {

            _this.$message.error(response.data.ErrMsg);

          } else {

            // console.log(response);

            var result = response.data.ResultData;

            _this.viewData.gridData = result.GridData;
            _this.viewData.count = result.Count;

          }   

          _this.loading = false;
          
        })
        .catch(function (error) {

          console.log(error);

        }); 

      }catch(err){

        //_this.$message.error(err.message);
        alert(err.message);

      } 

    },

    //
    handleCurrentChange:function(pageNo) {
        var _this = this;
        _this.pagination.curPage = pageNo;
        _this.search();
    },    

    //size
    handleSizeChange:function(val) {
        var _this = this;
        _this.pagination.perPageNum = val;
        _this.search();
    },  
    
    //
    handleSelectionChange:function(val) {
        var _this = this;
        _this.formData.multipleSelection = val;
    },    

    //
    viewDialog(row){
      var _this = this;

      _this.dialogForm.Id = row.Id;
      _this.dialogForm.AnalysisContent = row.AnalysisContent;

      //console.log(row.AnalysisContent);

      _this.dialogForm.dialogVisible = true;

    },

    ///
    viewDialogUpd(type,row){
      
      var _this = this;

      _this.dialogFormUpd.type = type;

      _this.dialogFormUpd.PositionStatus = 1;
      _this.dialogFormUpd.PositionStartTime = "";
      _this.dialogFormUpd.PositionEndTime = "";
      _this.dialogFormUpd.CompanyCode = "";
      _this.dialogFormUpd.CompanyName = "";
      _this.dialogFormUpd.PositionVol = "";
      _this.dialogFormUpd.CostPrice = "";
      _this.dialogFormUpd.CurrentPrice = "";
      _this.dialogFormUpd.TradeMkPlace = "";

      if(_this.dialogFormUpd.type=='2'){

        _this.dialogFormUpd.Id = row.Id;
        _this.dialogFormUpd.PositionStatus = row.PositionStatus;
        _this.dialogFormUpd.PositionStartTime = _this.commonUtil.formatData(row.PositionStartTime);
        _this.dialogFormUpd.PositionEndTime = _this.commonUtil.formatData(row.PositionEndTime);
        _this.dialogFormUpd.CompanyCode = row.CompanyCode;
        _this.dialogFormUpd.CompanyName = row.CompanyName;
        _this.dialogFormUpd.PositionVol = row.PositionVol;
        _this.dialogFormUpd.CostPrice = row.CostPrice;
        _this.dialogFormUpd.CurrentPrice = row.CurrentPrice;
        _this.dialogFormUpd.TradeMkPlace = row.TradeMkPlace;  

      }

      _this.dialogFormUpd.dialogVisible = true;

    },

    //
    handleClose(done) {
      
      this.$confirm('')
        .then(() => {
          done();
        })
        .catch(() => {});
    },

    onEditorChange(html) {

      var _this = this;
      
      _this.dialogForm.AnalysisContent = html;

      // console.log(_this.dialogForm.content);

    },

    //
    saveAnalysisContent(){

      var _this = this;

      try{
        
          //console.log(_this.dialogForm.AnalysisContent);

          _this.loading = true;

          var data = {
              Id: _this.dialogForm.Id,
              AnalysisContent: _this.dialogForm.AnalysisContent
            }

          _this.$http.post(_this.posturl + "/saveanlysiscontent",data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              // console.log(response);

              _this.search();
              _this.dialogForm.dialogVisible = false;

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },

    //
    indexMethod(index) {
        var _this = this;
        return (_this.pagination.curPage - 1) * _this.pagination.perPageNum + index + 1;
    },

    ///
    savePositionAnalysis(){

      var _this = this;

      try{
        
          //console.log(_this.dialogForm.AnalysisContent);

          _this.loading = true;

          var action = _this.dialogFormUpd.type=='1'?'/add':'/update';

          var data = _this.dialogFormUpd;

          _this.$http.post(_this.posturl + action,data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              // console.log(response);

              _this.search();
              _this.dialogFormUpd.dialogVisible = false;

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },

  }
}
</script>

<style scoped>

  .boxmain {
    width:1100px;
    margin: 0px auto;
    text-align: left;
    font-size: 15px;
  }

</style>
